html {
  --header-height: 60px;
  --color-blue: #00acdc;
  font-family: Roboto, sans-serif;
}

body {
  color: #fff;
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo__img {
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  height: var(--header-height);
  text-transform: uppercase;
  color: #000;
  text-align: center;
  font-size: 12px;
  font-weight: 500;
  line-height: var(--header-height);
  text-decoration: none;
  display: block;
  position: relative;
}

.is-active, .nav__link:hover {
  color: var(--color-blue);
}

.is-active:after {
  content: "";
  background-color: var(--color-blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  right: 0;
}
